import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, DateField } from 'src/app/common-module/table-list/list-field-type';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';



@Component({
  selector: 'app-registeration',
  templateUrl: './registeration.component.html'
})
export class RegisterationComponent implements OnInit {

  PageTitle = "Manage Product Registration (Sorting)";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private productregistrationService: ProductRegistrationService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Order No",
        key: "order.po_no",
      }),
      new TextField({
        label: "SKU",
        key: "sku_no",
      }),
      new TextField({
        label: "Product",
        key: "product.label",
      }),
      new TextField({
        label: "Serial Number",
        key: "serial_no",
      }),
      new TextField({
        label: "Tag ID",
        key: "tag_id",
      }),
      new TextField({
        label: "Qty",
        key: "qty",
      }),
      new DateField({
        label: "Register Date",
        key: "created_date",

      }) ,
    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/inbound/registeration/edit/',
        id: 'product_register_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'sorting/',
        id: 'product_register_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.productregistrationService.getProductRegistrationList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list ? response.data.list : [];
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




